.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: none;
    transition: .5s ease;
    background-color: #393839;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 20px;
}
.our-services-card:hover .card-overlay{
    opacity: 50%;
}
.our-services-card:hover .our-services-card-heading{
    bottom: 40px;
    left: 20px;
    visibility: visible;
    transition: 0.3s;
}
.our-services-card:hover .our-services-card-text{
    bottom: 20px;
    left: 20px;
    visibility: visible;
    transition: 0.3s
}
    
